.pi-wrapper {
  max-width: 478px;
  max-height: 318px;
  width: 478px;
  height: 318px;
  overflow: hidden;
  position: relative;
}

.pi-ambient {
  position: absolute;
  left: 50%;
  top: 55%;
  transform: translate(-50%, -50%);
  width: 70%;
  overflow: hidden;
  animation: fadein 1s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.pi-active-container {
  animation: fadein 1s;
}

.pi-greeting {
  text-align: center;
  color: rgb(156, 156, 156);
  font-weight: 400;
  font-size: 20px;
  font-family: 'Montserrat', sans-serif;
}

.pi-transcript-wrapper {
  position: absolute;
  left: 50%;
  top: 45%;
  transform: translate(-50%, -50%);
  width: 70%;
}

.pi-transcript {
  color: rgb(206, 206, 207);
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  font-size: 23px;
}

.pi-listening {
  position: absolute;
  bottom: 0px;
  width: 100%;
}

.pi-processing {
  position: absolute;
  left: 50%;
  bottom: -20px;
  transform: translate(-50%, 0px);
  width: 100px;
  overflow: hidden;
}

.pi-sleep-container {
  width: 478px;
  height: 318px;
  background-color: black;
}

/*Huge thanks to @tobiasahlin at http://tobiasahlin.com/spinkit/ */

.spinner {
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
}

.spinner>div {
  width: 15px;
  height: 15px;
  background-color: rgb(61, 62, 167);
  /*spinner bounce 3 */
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 2s infinite ease-in-out both;
  animation: sk-bouncedelay 2s infinite ease-in-out both;
  margin: 2px;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
  background-color: #A416FD;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
  background-color: #75E2FF;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0.5)
  }
  40% {
    -webkit-transform: scale(1.0)
  }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0.5);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }
}

.pi-clock {
  width: 100%;
  color: rgb(194, 194, 194);
  font-family: 'Montserrat', sans-serif;
  font-size: 65px;
  font-weight: 300;
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  margin: 0px;
  text-align: center;
}

.pi-ambient-clock {
  position: absolute;
  left: 50%;
  bottom: -8%;
  transform: translate(-50%, 0px);
  width: 30%;
  overflow: hidden;
  animation: fadein 1s;
  z-index: 1;
}